/* MovieCarousel.css */

.movie-carousel {
    display: flex;
    overflow-x: auto;
    padding: 10px;
    white-space: nowrap; /* Ensure items stay on one line */
    max-width: 70%; /* Ensure it does not exceed the parent container */
    margin: 0 auto; /* Center the carousel container */
}

.carousel-item {
    flex: 0 0 auto; /* Prevent flex item from growing or shrinking */
    margin-right: 10px; /* Spacing between items */
}

.carousel-item img {
    width: 150px; /* Adjust width as needed */
    height: auto; /* Maintain aspect ratio */
    border-radius: 8px; /* Optional: Add rounded corners */
    object-fit: cover; /* Ensure images fit the container */
}
